.movie-item {
    width: 341px;
    height: 192px;
    flex: 0 0 auto; /* flex-grow, flex-shrink, flex-basis */
    margin-right: 10px; /* 아이템 간 간격 */
    transition: transform 0.225s ease;
}

.movie-item:hover {
    transform: scale(1.125);
    box-shadow: 100px 100px 100px 100px rgba(0, 0, 0, 0.436);
}

.rank {
    font-size: 30px;
    text-shadow: 2px 2px 5px black;
    position: relative;
    top: 102px;
    left: 280px;
    z-index: 0;
    font-size: 100px;
    font-style: italic;
    color: rgba(240, 248, 255);
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
    -webkit-user-select: none;  /* Chrome, Safari, Opera 등 */
    -moz-user-select: none;     /* Firefox */
    -ms-user-select: none;      /* Internet Explorer/Edge */
    user-select: none;          /* 비표준 브라우저를 위한 일반적인 속성 */
}
.movie-item {
    position: relative;
}

.movie-item img {
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: -1;
    right: 0;
    background-blend-mode: multiply;
}