#profile-setting img {
    width: 200px;
    height: 200px;
    object-fit: cover;
    border-radius: 30px;
    margin: 10px;
    cursor: pointer;
}

#profile-setting .image-container {
    height: 400px; /* 원하는 높이로 설정 */
    overflow-y: auto;
    display: flex;
    flex-wrap: wrap;
    gap: 10px; /* 이미지 간격 */
}

#profile-setting .selected-image img {
    width: 100%;
    max-width: 400px;
    height: auto;
    border-radius: 20px;
    margin-bottom: 20px;
}

#profile-setting img.selected {
    border: 3px solid blue; /* 선택된 이미지에 대한 테두리 스타일 */
}