#account-management-page {
    display: flex;
    width: 100%;
    height: 100vh;
    justify-content: center;
    align-items: center;
    background-color: #141414; /* 넷플릭스의 배경색과 유사 */
}

#account-management-page-content {
    width: 900px;
    height: 600px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #fff; /* 밝은 배경색 */
    border-radius: 8px; /* 모서리 둥글게 */
    padding: 20px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* 그림자 효과 */
    color: black;
}

h1 {
    margin-bottom: 30px;
}

#account-form {
    width: 100%;
    max-width: 400px;
}

.form-group {
    margin-bottom: 20px;
}

#sex-form {
    display: flex;
    flex-direction: row;
}

#sex-form * {
    margin-right: 10px;
}

.form-group label {
    display: block;
    margin-bottom: 5px;
}

.form-group input[type="text"],
.form-group input[type="password"] {
    width: 100%;
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: 4px;
}

.form-group input[type="radio"] {
    margin-right: 10px;
}

button {
    padding: 10px 20px;
    background-color: #e50914; /* 넷플릭스의 버튼 색과 유사 */
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
}