#dark-background-space {
    position: fixed; /* 화면에 고정 */
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000; /* 필요한 경우 다른 요소들 위에 배치 */
    background-color: rgba(0, 0, 0, 0.505);
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.25s ease;
}

#dark-background-space.active {
    /* 활성화 상태 */
    opacity: 1;
    visibility: visible;
}

.movie-info-card {
    background-color: rgb(24 24 24);
    width: 800px;
    height: 70%; /* 고정된 높이 */
    overflow-y: auto; /* 내용이 높이를 초과할 경우 스크롤바 표시 */
    padding: 80px; /* 내부 여백 추가 */
}
.movie-info-card h1 {
    font-size: 45px;
    margin-bottom: 30px;
}
.movie-info-card h2 {
    font-size: 30px;
    margin-top: 20px;
}
.movie-info-card p {
    font-size: 20px;
    margin-top: 5px;
}
.movie-info-card img {
    margin-bottom: 35px;
}

.movie-info-card #info-container {
    color: rgb(197, 197, 197);

}