#select-profile-page {
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

#select-profile-page img {
    width: 200px;
    height: 200px;
    border-radius: 30px;
    cursor: pointer;
    object-fit: cover;
}

#select-profile-page .profile-list-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}

#select-profile-page .profile-item {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 15px;
}

#select-profile-page .profile-item span {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 15px;
    font-size: 20px;
    cursor: pointer;
}

#select-profile-page button {
    margin-top: 20px;
}

.profile-edit {
    visibility: hidden;
    color: rgb(183, 183, 183);
}

.profile-item:hover .profile-edit {
    visibility: visible;
}